<template>
  <div class="bug-report" style="margin:0">
    <div class="vue-ui-grid col-2 default-gap">

      <VueFormField
        v-if="isMobile"
        :title="i18n('build-title')"
        :subtitle="i18n('build-subtitle')"
      >
        <VueInput
          v-model="attrs.build"
          required
        />
      </VueFormField>

      <VueFormField
        v-else
        :title="i18n('version-title')"
        :subtitle="i18n('version-subtitle')"
      >
        <!-- <VueTypeAhead
          v-model="attrs.version"
          :suggestions="suggestions"
          :loading="loadingVersion"
          show-all
          show-max="30"
          restrict-choice
          required
        /> -->

        <VueInput
          v-model="attrs.version"
          required
        />
      </VueFormField>

      <VueFormField
        v-if="isMobile"
        :title="i18n('mobile-os-title')"
      >
        <VueInput
          v-model="attrs.mobileOS"
          required
        />

        <i18n
          slot="subtitle"
          id="mobile-os-subtitle"
        />
      </VueFormField>

      <VueFormField
        v-if="repo === 'zeppelin-inc/feelit_web'"
        :title="i18n('browser-and-os-title')"
      >
        <VueInput
          v-model="attrs.browserAndOS"
          required
        />

        <i18n
          slot="subtitle"
          id="browser-and-os-subtitle"
        />
      </VueFormField>

      <VueFormField
        :title="i18n('network-title')"
      >
        <VueInput
          type="url"
          v-model="attrs.network"
          required
        />

        <i18n
          slot="subtitle"
          id="network-subtitle"
        />
      </VueFormField>

      <VueFormField
        v-if="isMobile"
        :title="i18n('screenshot-title')"
      >
        <VueInput
          v-model="attrs.screenshot"
        />

        <i18n
          slot="subtitle"
          id="screenshot-subtitle"
        />
      </VueFormField>

      <VueFormField
        v-if="isWeb"
        :title="i18n('repro-title')">
        <VueInput
          type="url"
          v-model="attrs.reproduction"
          :disabled="isCLI && reproNotAvailable"
          required
        />

        <template slot="subtitle">
          <i18n
            :id="isCLI ? 'cli-repro-subtitle' : 'repro-subtitle'"
            @click-modal="show = true"
          />
          <VueSwitch v-if="isCLI" v-model="reproNotAvailable">
            <i18n id="cli-no-repro"/>
          </VueSwitch>
        </template>
      </VueFormField>

      <VueFormField
        class="span-2"
        :title="i18n('steps-title')"
      >
        <VueInput
          type="textarea"
          rows="4"
          v-model="attrs.steps"
          required
        />
        <i18n slot="subtitle" id="steps-subtitle"/>
      </VueFormField>

      <VueFormField
        :title="i18n('expected-title')"
      >
        <VueInput
          type="textarea"
          rows="4"
          v-model="attrs.expected"
          required
        />
      </VueFormField>

      <VueFormField
        :title="i18n('actual-title')"
      >
        <VueInput
          type="textarea"
          rows="4"
          v-model="attrs.actual"
          required
        />
      </VueFormField>

      <VueFormField
        class="span-2"
        :title="i18n('extra-title')"
        :subtitle="i18n('extra-subtitle')"
      >
        <VueInput
          type="textarea"
          rows="4"
          v-model="attrs.extra"
        />
      </VueFormField>
    </div>

    <VueModal
      v-if="show"
      :title="i18n('repro-modal-title')"
      class="medium"
      @close="show = false"
    >
      <div class="default-body">
        <i18n id="repro-modal"/>
      </div>
    </VueModal>
  </div>
</template>

<script>
import { gt } from 'semver'
import { generate } from '../helpers'

export default {
  props: ['repo'],

  data () {
    return {
      show: false,
      attrs: {
        version: '',
        build: '',
        screenshot: 'Uploaded via Github',
        reproduction: '',
        network: '',
        steps: '',
        expected: '',
        actual: '',
        extra: '',
        browserAndOS: '',
        mobileOS: '',
        nodeAndOS: '',
      },
      versions: [],
      loadingVersion: false,
      reproNotAvailable: false
    }
  },

  computed: {
    suggestions () {
      return this.versions
        .slice()
        .sort((a, b) => gt(a.value, b.value) ? -1 : 1)
    },

    isCLI () {
      return this.repo === 'zeppelin-inc/feelit_cli'
    },

    isMobile () {
      return this.repo === 'zeppelin-inc/feelit_IOS' || this.repo === 'zeppelin-inc/feelit_android'
    },

    isWeb () {
      return this.repo === 'zeppelin-inc/feelit_server' || this.repo === 'zeppelin-inc/feelit_web'
    }

  },

  watch: {
    repo () {
      this.versions = []
      this.attrs.version = ''
      this.attrs.build = ''
      // this.fetchVersions()
    }
  },

  created () {
    // this.fetchVersions()
  },

  methods: {
    /*async fetchVersions (page = 1) {
      this.loadingVersion = true
      const repo = this.repo
      const response = await fetch(`https://api.github.com/repos/${repo}/releases?page=${page}&per_page=100`)
      const releases = await response.json()

      if (this.repo !== repo) return

      if (!releases || !(releases instanceof Array)) return false

      this.versions = this.versions.concat(releases.map(
        r => ({ value: /^v/.test(r.tag_name) ? r.tag_name.substr(1) : r.tag_name })
      ))

      const link = response.headers.get('Link')

      if (link && link.indexOf('rel="next"') > -1) {
        await this.fetchVersions(page + 1)
      } else {
        this.loadingVersion = false
      }
    },*/

    generate () {
      const {
        version,
        build,
        mobileOS,
        network,
        screenshot,
        reproduction,
        steps,
        expected,
        actual,
        extra,
        browserAndOS,
        nodeAndOS
      } = this.attrs

      return generate(`
${version ? `### Version
${version}` : ``}

${build ? `### Build number
${build}` : ``}

${mobileOS ? `### Phone model and OS
${mobileOS}` : ``}

${network ? `### Network speed test
[${network}](${network})` : ``}

### Screenshots
${screenshot}

${reproduction ? `### Reproduction link
[${reproduction}](${reproduction})` : ``}

${browserAndOS ? `### Browser and OS info
${browserAndOS}` : ``}

${nodeAndOS ? `### Node and OS info
${nodeAndOS}` : ``}

### Steps to reproduce
${steps}

### What is expected?
${expected}

### What is actually happening?
${actual}

${extra ? `---\n${extra}` : ''}
  `.trim())
    }
  },
}
</script>
