import qs from 'qs'

export function getQuery() {
  return qs.parse(window.location.search.slice(1))
}

export function updateQuery(query) {
  const newUrl =
    window.location.origin +
    '/issues?' +
    qs.stringify({ ...getQuery(), ...query }, { encode: false })
  window.history.pushState(
    {
      path: newUrl
    },
    '',
    newUrl
  )
}
